import {
    Box,
  Button,
  Modal,
  ModalBody,
  ModalCloseButton,
  ModalContent,
  ModalFooter,
  ModalHeader,
  ModalOverlay,
} from "@chakra-ui/react";
import { FC } from "react";

interface ExportModalProps {
  title?: string;
  isOpen: boolean;
  onClose: () => void;
  onOpen: () => void;
  blobUrl: string;
}
const ExportModal: FC<ExportModalProps> = ({
  title,
  isOpen,
  onClose,
  onOpen,
  blobUrl,
}) => {
  return (
    <Modal
      onClose={onClose}
      isOpen={isOpen}
      isCentered
      motionPreset="slideInBottom"
      scrollBehavior="inside"
      size="xl"
    >
      <ModalOverlay />
      <ModalContent>
        <ModalHeader>{title ?? ""}</ModalHeader>
        <ModalCloseButton />
        <ModalBody>
          <Box>
            {blobUrl !== "" ? (
              <video
                src={blobUrl}
                controls
                style={{
                  borderBlock: "solid",
                  borderWidth: 1,
                  borderColor: "#000",
                }}
              ></video>
            ) : (
              <></>
            )}
          </Box>
        </ModalBody>
        <ModalFooter>
          <Button onClick={onClose}></Button>
        </ModalFooter>
      </ModalContent>
    </Modal>
  );
};

export default ExportModal